import React from 'react';
import { useAuth } from '../../../context/AuthContext';
import { useCycle } from '../../../context/CycleContext';
import { useAppointment } from '../../../context/AppointmentContext';
import {
  Calendar,
  Heart,
  MessageCircle,
  TestTube,
  Bell,
  User,
} from 'lucide-react';
import { Link } from 'react-router-dom';
import './index.css';

const Dashboard = () => {
  const { user } = useAuth();
  const { getDaysUntilNextPeriod, getDaysUntilOvulation, isInFertilityWindow } =
    useCycle();
  const { getUpcomingAppointments } = useAppointment();

  const upcomingAppointments = getUpcomingAppointments();
  const daysUntilPeriod = getDaysUntilNextPeriod();
  const daysUntilOvulation = getDaysUntilOvulation();
  const inFertilityWindow = isInFertilityWindow();

  const quickActions = [
    {
      title: 'Theo di chu k',
      description: 'Ghi nhn chu k v triu chng',
      icon: Calendar,
      link: '/cycle-tracking',
      color: 'pink',
    },
    {
      title: 't lch t vn',
      description: 'T vn vi chuyn gia',
      icon: MessageCircle,
      link: '/consultation',
      color: 'blue',
    },
    {
      title: 'Xt nghim STIs',
      description: 't lch xt nghim',
      icon: TestTube,
      link: '/sti-testing',
      color: 'green',
    },
    {
      title: 'Hi p',
      description: 'Gi cu hi cho t vn vin',
      icon: Heart,
      link: '/qa',
      color: 'purple',
    },
  ];

  return (
    <div className="dashboard-container">
      {/* Header */}
      <header className="dashboard-header">
        <div className="dashboard-header-content">
          <div>
            <h1 className="dashboard-title">Dashboard</h1>
          </div>
          <div className="header-actions">
            <Bell className="notification-icon" />
            <Link to="/profile" className="user-profile-link">
              <User className="h-6 w-6" />
              <span>{user?.name}</span>
            </Link>
          </div>
        </div>
      </header>

      <div className="dashboard-main">
        {/* Welcome Section */}
        <div className="welcome-section">
          <h2 className="welcome-title">Cho mng tr li, {user?.name}!</h2>
          <p className="welcome-subtitle">
            y l tng quan v sc khe v lch trnh ca bn
          </p>
        </div>

        {/* Health Overview Cards */}
        <div className="health-overview">
          {/* Cycle Card */}
          <div className="health-card cycle-card">
            <div className="health-card-content">
              <div className="health-card-info">
                <h3>Chu k kinh nguyt</h3>
                {daysUntilPeriod !== null ? (
                  <p className="health-card-number">{daysUntilPeriod} ngy</p>
                ) : (
                  <p className="health-card-number">Cha thit lp</p>
                )}
                <p className="health-card-label">n k kinh tip theo</p>
              </div>
              <Calendar className="health-card-icon" />
            </div>
          </div>

          {/* Ovulation Card */}
          <div
            className={`health-card ${
              inFertilityWindow ? 'ovulation-card' : ''
            }`}
          >
            <div className="health-card-content">
              <div className="health-card-info">
                <h3>Rng trng</h3>
                {daysUntilOvulation !== null ? (
                  <p className="health-card-number">
                    {daysUntilOvulation} ngy
                  </p>
                ) : (
                  <p className="health-card-number">Cha thit lp</p>
                )}
                <p className="health-card-label">
                  {inFertilityWindow
                    ? 'ang trong thi k mu m'
                    : 'n thi k rng trng'}
                </p>
              </div>
              <Heart className="health-card-icon" />
            </div>
          </div>

          {/* Appointments Card */}
          <div className="health-card appointment-card">
            <div className="health-card-content">
              <div className="health-card-info">
                <h3>Lch hn</h3>
                <p className="health-card-number">
                  {upcomingAppointments.length}
                </p>
                <p className="health-card-label">lch hn sp ti</p>
              </div>
              <MessageCircle className="health-card-icon" />
            </div>
          </div>
        </div>

        {/* Quick Actions */}
        <div className="quick-actions-section">
          <h3 className="section-title">Thao tc nhanh</h3>
          <div className="quick-actions-grid">
            {quickActions.map((action, index) => (
              <Link key={index} to={action.link} className="quick-action-card">
                <div className={`action-icon ${action.color}`}>
                  <action.icon />
                </div>
                <h4 className="action-title">{action.title}</h4>
                <p className="action-description">{action.description}</p>
              </Link>
            ))}
          </div>
        </div>

        {/* Recent Activity & Upcoming Appointments */}
        <div className="content-grid">
          {/* Upcoming Appointments */}
          <div className="content-card">
            <div className="content-card-header">
              <h3 className="content-card-title">Lch hn sp ti</h3>
              <Link to="/appointments" className="view-all-link">
                Xem tt c
              </Link>
            </div>
            {upcomingAppointments.length > 0 ? (
              <div className="appointment-list">
                {upcomingAppointments.slice(0, 3).map(appointment => (
                  <div key={appointment.id} className="appointment-item">
                    <div className="appointment-info">
                      <p className="appointment-doctor">
                        Dr. {appointment.counselorName}
                      </p>
                      <p className="appointment-time">
                        {appointment.date} lc {appointment.time}
                      </p>
                    </div>
                    <span className="appointment-status">
                      {appointment.status}
                    </span>
                  </div>
                ))}
              </div>
            ) : (
              <p className="empty-state">Khng c lch hn no</p>
            )}
          </div>

          {/* Health Tips */}
          <div className="content-card">
            <h3 className="content-card-title">Mo sc khe</h3>
            <div className="health-tips">
              <div className="health-tip blue">
                <h4 className="tip-title">Theo di chu k u n</h4>
                <p className="tip-content">
                  Ghi chp chu k kinh nguyt gip bn hiu r hn v c th
                  mnh
                </p>
              </div>
              <div className="health-tip green">
                <h4 className="tip-title">Tm sot nh k</h4>
                <p className="tip-content">
                  Xt nghim STIs nh k l cch tt nht  bo v sc khe
                  sinh sn
                </p>
              </div>
              <div className="health-tip purple">
                <h4 className="tip-title">T vn chuyn gia</h4>
                <p className="tip-content">
                  ng ngn ngi t cu hi vi cc chuyn gia ca chng ti
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
